/*jshint esversion: 6*/
const close = document.querySelector('.modal__close'),
      open = document.querySelector('.header__phone'),
      modal = document.querySelector('.wrapper-modal'),
      form = document.querySelector('.modal'),
      formInputs = document.querySelectorAll('.modal__right_input'),
      inputEmail = document.querySelector('.modal__right_input-mail'),
      inputPhone = document.querySelector('.modal__right_input-phone'),
      inputCheckbox = document.querySelector('.modal__right_checkbox'),
      inputCheckText = document.querySelector('.modal__right_text-p');


open.addEventListener('click', () => {
    modal.classList.add('active');
});

close.addEventListener('click', () => {
    modal.classList.remove('active');
});


//валидация

function validateEmail(email) {
    let re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
}

function validatePhone(phone) {
    let re = /^\+?[78][-\(]?\d{3}\)?-?\d{3}-?\d{2}-?\d{2}$/;
    return re.test(String(phone));
}

form.onsubmit = function () {
    let phoneValue = inputPhone.value,
        emailValue = inputEmail.value,
        emptyInputs = Array.from(formInputs).filter(input => input.value === '');

    formInputs.forEach(function(input) {
        if (input.value === '') {
            input.classList.add('input-error');
            console.log('Что то пошло не так!!!');
        } else {
            input.classList.remove('input-error');
        }
    });

    if (emptyInputs.length !== 0) {
        return false;
    }

    if (!validatePhone(phoneValue)) {
        console.log('Телефон не корректный!');
        inputPhone.classList.add('input-error');
        return false;
    } else {
        inputPhone.classList.remove('input-error');
    }

    if (!validateEmail(emailValue)) {
        console.log('Email не корректный!');
        inputEmail.classList.add('input-error');
        return false;
    } else {
        inputEmail.classList.remove('input-error');
    }

    if (!inputCheckbox.checked) {
        console.log('Поставьте галочку для отправки!');
        inputCheckText.classList.add('input-error');
        return false;
    } else {
        inputCheckText.classList.remove('input-error');
    }

};
